
.footer {
  background: #1a365d;
  color: white;
  padding: 4rem 0 2rem;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.footer-section {
  margin-bottom: 2rem;
}

.footer-heading {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.footer-subheading {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.footer-description {
  color: #e2e8f0;
  line-height: 1.6;
}

.footer-social {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.social-icon {
  color: white;
  transition: color 0.2s;
}

.social-icon:hover {
  color: #4299e1;
}

.footer-links {
  list-style: none;
  padding: 0;
}

.footer-links li {
  margin-bottom: 0.5rem;
}

.footer-links a {
  color: #e2e8f0;
  text-decoration: none;
  transition: color 0.2s;
}

.footer-links a:hover {
  color: #4299e1;
}

.footer-bottom {
  border-top: 1px solid #2d4a73;
  margin-top: 2rem;
  padding-top: 2rem;
}

.footer-legal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.legal-links {
  display: flex;
  gap: 1.5rem;
  list-style: none;
  padding: 0;
}

.app-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 0.5rem;
}
